"use client";
import { useState } from "react";

export default function Home() {
  const [count, setCount] = useState(0);
  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        justifyContent: "center",
        height: "100vh", // agar bisa vertikal di tengah
        textAlign: "center",
      }}
    >
      <h1> Home</h1>

      <p>Counter: {count}</p>
      <button
        onClick={() => {
          setCount(count + 1);
        }}
      >
        Increment
      </button>
      <button
        onClick={() => {
          const clear = 0;
          setCount(clear);
        }}
      >
        {" "}
        reset
      </button>
    </div>
  );
}
